.c-full-image-banner{
  position: relative;

    &__image {
      &.has-mobile.is-desktop { display: none; }  
    }

    &__image-container {
      padding-bottom: 160%;
    }

    &__content{
      @include t-absolute-center-x-y();
      color:$s-color--font-invert;
      padding: 0 $s-gutter--edge-mobile;
      width: 100%;
      max-width: 73.625rem;
      margin: 0 auto;
      text-align: center;
    }

    .o-wave{
      position: absolute;
      z-index: $s-z--illustration-overlay;

      &:nth-of-type(1){
        top: 0;
      }

      &:nth-of-type(3){
        bottom: 0;
      }
    }

  @include t-media-query($s-small-up) {

    &__image {
      &.is-mobile { display: none; }
      &.has-mobile.is-desktop { display: block; }
    }

    &__image-container{
      padding-bottom: 50%;
    }

    &__content{
      top: 45.9%;
    }
  }

  @include t-media-query($s-large-up) {
    
    &__image {
      &.is-mobile { display: none; }
      &.has-mobile.is-desktop { display: block; }
    }

    &__image-container{
      padding-bottom: 46.5%;
    }

    &__content{
      padding: 0;
      top: 48.9%;
    }
  }
}